<template>
  <div
    class="ebook"
    ref="ebookView"
  >
    <ebook-title></ebook-title>
    <ebook-reader></ebook-reader>
    <ebook-menu></ebook-menu>
    <ebook-bookmark></ebook-bookmark>
    <ebook-footer></ebook-footer>
  </div>
</template>

<script>
import EbookReader from '../../components/ebook/EbookReader'
import EbookTitle from '../../components/ebook/EbookTitle'
import EbookMenu from '../../components/ebook/EbookMenu'
import EbookBookmark from '../../components/ebook/EbookBookmark'
import EbookFooter from '../../components/ebook/EbookFooter'
import { getReadTime, saveReadTime } from '../../utils/localStorage'
import { ebookMixin } from '../../utils/mixin'
export default {
  mixins: [ebookMixin],
  components: {
    EbookReader,  //核心阅读器
    EbookMenu,   //菜单栏
    EbookTitle,   //头部
    EbookBookmark,  //书签
    EbookFooter //页脚
  },
  watch: {
    offsetY (v) {
      // if (this.isPaginating !== null && this.isPaginating === false && !this.menuVisible) {
      //   if (v === 0) {
      //     this.restore()
      //   } else if (v > 0) {
      //     this.move(v)
      //   }
      // }
      if (!this.menuVisible) {
        if (v === 0) {
          this.restore()
        } else if (v > 0) {
          this.move(v)
        }
      }

    }
  },
  methods: {
    restore () {
      this.$refs.ebookView.style.top = 0
      this.$refs.ebookView.style.transition = 'all .2s linear'
      setTimeout(() => {
        this.$refs.ebookView.style.transition = ''
      }, 200)
    },
    move (offsetY) {
      this.$refs.ebookView.style.top = offsetY + 'px'
    },
    startLoopReadTime () {
      let readTime = getReadTime(this.fileName)
      if (!readTime) readTime = 0
      this.task = setInterval(() => {
        readTime++
        if (readTime % 30 == 0) {
          saveReadTime(this.fileName, readTime)
        }
      }, 1000);
    }
  },
  mounted () {
    this.startLoopReadTime()
  },
  beforeDestroy () {
    if (this.task) {
      clearInterval(this.task)
    }
  }
}
</script>

<style lang='scss' rel='stylesheet/scss' scoped>
@import "../../assets/styles/global";
.ebook {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
}
</style>
